import React from 'react';

const AboutDialog = ({ ok }) => {
  return (
    <div className="fixed top-0 left-0 w-full h-full flex items-center justify-center bg-gray-900 bg-opacity-50 z-50">
      <div className="bg-white border border-gray-300 rounded p-4 shadow-lg text-center">
        <h2 className="text-xl font-bold mb-4">About</h2>
        <div className="flex flex-col items-center">
          <p className="mb-4">
            This is an API client, a budget{' '}
            <a href="https://www.postman.com/" className="text-blue-500 underline" target="_blank" rel="noopener noreferrer">
              Postman
            </a>
            . I created it out of boredom.
          </p>
          <p className="mb-4">
            Date: 18.05.2023 - {' '}
            <a href="https://github.com/e-krl/reapi" className="text-blue-500 underline" target="_blank" rel="noopener noreferrer">
              GitHub
            </a>
            .
          </p>
          <button className="px-4 py-2 bg-blue-500 text-white rounded" onClick={ok}>
            Ok
          </button>
        </div>
      </div>
    </div>
  );
};

export default AboutDialog;
